.block2{
  position: relative;
  padding: 70px 40px 50px;
  background: var(--color-white-80);
  z-index: 1;
  ul{
    display: grid;
    gap: 32px;
    grid-template-columns: auto auto auto auto;
    padding: 44px 0 0;
    li{
      .label{
        margin: 0 0 31px;
        padding: 0 0 10px;
        border-bottom: 1px solid var(--color-black-10);
        font-size: 18px;
        line-height: 28px;
        color: var(--color-text-gray);
      }
      .number{
        padding: 0 0 6px;
        font-size: clamp(48px, calc(48px + 16*((100vw - 375px)/1065)), 64px);
        font-weight: 500;
        line-height: 112.5%;
        letter-spacing: -2px;
        *{
          font-size: clamp(48px, calc(48px + 16*((100vw - 375px)/1065)), 64px);
          font-weight: 500;
          line-height: 112.5%;
          letter-spacing: -2px;
        }
      }
    }
  }
}