.block6{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  .item{
    position: relative;
    &:first-child{
      .h1,
      .desc,
      a.btn{
        position: relative;
        z-index: 1;
      }
      .desc{
        padding: 32px 0 0;
        font-size: 18px;
      }
      a.btn{
        margin: 48px 0 0;
      }
      .flask{
        position: relative;
        max-width: 110px;
        margin: 0 auto;
        transform: rotate(28deg);
        z-index: 1;
        img{
          max-width: 100%;
        }
        .filter{
          position: absolute;
          bottom: 2px;
          left: 20px;
          width: 75px;
          height: 81%;
          border-radius: 0 0 50px 50px;
          mix-blend-mode: lighten;
          overflow: hidden;
          transition: none;
          .fill{
            position: absolute;
            bottom: -50px;
            left: -200%;
            width: 600%;
            height: 0;
            background: var(--color-text-hover);
            transform: rotate(-24deg);
            transition: none;
            svg{
              position: relative;
              width: 61%;
              height: 9%;
              min-height: 3px;
              max-height: 150px;
              margin: -20px 0 0;
              transition: none;
              use{
                animation: waves 5s cubic-bezier(.55,.5,.45,.5) infinite;
                &:nth-child(1){
                  animation-delay: -2s;
                  animation-duration: 7s;
                }
                &:nth-child(2){
                  animation-delay: -3s;
                  animation-duration: 10s;
                }
                &:nth-child(3){
                  animation-delay: -4s;
                  animation-duration: 13s;
                }
                &:nth-child(4){
                  animation-delay: -5s;
                  animation-duration: 20s;
                }
              }
            }
          }
        }
      }
      .bganimation{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
    &:last-child{
      ul{
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 60px;
        padding: 64px 0 0;
        li{
          display: flex;
          align-items: center;
          .ico{
            position: relative;
            width: 102px;
            max-width: 102px;
            min-width: 102px;
            height: 102px;
            margin: 0 28px 0 0;
            &:before{
              position: absolute;
              bottom: 0;
              right: 0;
              display: block;
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background: linear-gradient(150.84deg,var(--color-animate-purple) 57.06%,var(--color-animate-purple-10) 145.73%);
              content: '';
            }
            .box{
              position: relative;
              width: 100%;
              height: 100%;
              display: flex;
              justify-content: center;
              align-items: center;
              border-radius: 50%;
              background: url(../img/icobg.svg);
              background-size: contain;
              overflow: hidden;
              span{
                position: absolute;
                top: 4px;
                left: 4px;
                width: calc(100% - 8px);
                height: calc(100% - 8px);
                border-radius: 50%;
                overflow: hidden;
                &:before,
                &:after{
                  position: absolute;
                  display: block;
                  content: '';
                }
                &:before{
                  top: 0;
                  left: 0;
                  width: 100%;
                  height: 100%;
                  background: var(--color-bg);
                }
                &:after{
                  bottom: -3px;
                  right: -3px;
                  width: 40px;
                  height: 40px;
                  border-radius: 50%;
                  filter: blur(10px);
                  background: linear-gradient(150.84deg,var(--color-animate-purple) 57.06%,var(--color-animate-purple-10) 145.73%);
                }
              }
            }
            img{
              position: absolute;
              top: 50%;
              left: 50%;
              width: 48px;
              height: 48px;
              margin: -24px 0 0 -24px;
            }
          }
          .info{
            .desc{
              padding: 13px 0 0;
            }
          }
        }
      }
    }
  }
}

@keyframes waves{
  0% {
    transform: translate3d(-90px,0,0);
  }
  100% {
    transform: translate3d(85px,0,0);
  }
}