.block9{
  .h1{
    position: relative;
    z-index: 1;
  }
  ul{
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 16px;
    padding: 32px 0 0;
    z-index: 1;
    li{
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px;
      background: var(--color-white-60);
      transition: transform .7s;
      overflow: hidden;
      &:hover{
        background: var(--color-white);
        transform: scale(.97);
        img{
          filter: none;
          opacity: 1;
        }
      }
      img{
        width: 100%;
        max-width: 150px;
        margin: 0 auto;
        filter: grayscale(100);
        mix-blend-mode: multiply;
        opacity: .5;
      }
    }
  }
}