.block10{
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: 762px;
  padding: 90px 60px;
  background: var(--color-white);
  overflow: hidden;
  .item{
    position: relative;
    z-index: 1;
    &:nth-child(1){
      .desc{
        max-width: 510px;
        padding: 32px 0 0;
      }
      a.btn{
        margin: 30px 0 0;
      }
    }
    &:nth-child(2){
      position: relative;
      display: flex;
      flex-direction: row;
      ul{
        display: flex;
        flex-direction: column;
        gap: 16px;
        max-width: 324px;
        margin: 0 0 0 auto;
        li{
          a{
            display: block;
            padding: 30px 24px;
            border-radius: 15px;
            background: var(--color-white-50);
            font-size: 24px;
            font-weight: 600;
            line-height: 32px;
            white-space: nowrap;
            &:hover{
              background: var(--color-white);
            }
          }
        }
      }
    }
  }
  img{
    position: absolute;
    top: 90px;
    left: 50%;
    max-width: 210px;
    transform: rotate(28deg);
    transition: none;
    z-index: 1;
  }
}