.block22{
  ul{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    padding: 30px 0 0;
    li{
      a{
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 30px 24px;
        background: var(--color-white);
        &:hover{
          transform: scale(.97);
        }
        span.date{
          font-weight: 300;
        }
        h3{
          padding: 22px 0 76px;
        }
        img{
          margin: auto 0 0;
        }
      }
    }
  }
  .actions{
    display: flex;
    justify-content: center;
    padding: 40px 0 0;
    a.btn{
      border-color: var(--color-text-hover);
      color: var(--color-text-hover);
      &:hover{
        color: var(--color-white);
      }
    }
  }
}