.block15{
  form{
    margin: 30px 0 0;
    padding: 40px;
    background: var(--color-white-70);
    .swiper{
      position: relative;
      &.hidden{
        .steps{
          color: var(--color-transparent);
          span{
            color: var(--color-transparent);
          }
        }
        .navs{
          visibility: hidden;
          opacity: 0;
        }
      }
      .steps{
        width: 100%;
        padding: 0 0 32px;
        border-bottom: 1px solid var(--color-black-10);
        font-size: 16px;
        font-weight: 600;
        color: var(--color-text-hover);
        span{
          padding: 0 0 0 4px;
          color: var(--color-text-gray);
        }
      }
      .swiper-slide{
        display: flex;
        flex-wrap: nowrap;
        gap: 48px;
        padding: 40px 0 0;
        &:last-child{
          .item{
            &:last-child{
              padding: 0;
            }
          }
        }
        .item{
          &:first-child{
            display: flex;
            flex-direction: column;
            width: 40%;
            .h3{
              color: var(--color-text-hover);
            }
            .desc{
              margin: auto 0 0;
              font-weight: 700;
              color: var(--color-text-hover);
            }
          }
          &:last-child{
            width: 60%;
            min-height: 308px;
            padding: 0 0 62px;
            .fieldset{
              display: grid;
              grid-template-columns: repeat(2, 1fr);
              gap: 16px;
              width: 100%;
              min-width: 0;
              .field{
                .checkboxes{
                  display: flex;
                  align-items: center;
                  gap: 26px;
                }
              }
            }
            img{
              max-width: 320px;
            }
            button.btn{
              margin: 30px 0 0;
            }
          }
        }
      }
      .navs{
        position: absolute;
        bottom: 0;
        right: 0;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        width: 100%;
        gap: 15%;
        z-index: 1;
        button{
          height: 24px;
          gap: 8px;
          &:hover{
            svg{
              use{
                fill: var(--color-text-main);
              }
            }
          }
          &:first-child{
            color: var(--color-text-gray);
            &:hover{
              color: var(--color-text-main);
              svg{
                use{
                  fill: var(--color-text-main);
                }
              }
            }
            svg{
              use{
                fill: var(--color-text-gray);
              }
            }
          }
          &:last-child{
            color: var(--color-text-hover);
            &:hover{
              color: var(--color-text-main);
              svg{
                use{
                  fill: var(--color-text-main);
                }
              }
            }
            svg{
              use{
                fill: var(--color-text-hover);
              }
            }
          }
          &.swiper-button-disabled{
            display: none;
          }
          svg{
            max-width: 24px;
            min-width: 24px;
            height: 24px;
            use{
              fill: var(--color-text-hover);
            }
          }
        }
      }
    }
  }
}