.block5{
  ul{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    grid-template-areas: "one three" "two three";
    li{
      &:nth-child(1){
        grid-area: one;
      }
      &:nth-child(2){
        grid-area: two;
      }
      &:nth-child(3){
        grid-area: three;
      }
      a{
        position: relative;
        display: block;
        transition: transform .7s;
        overflow: hidden;
        &:hover{
          transform: scale(.97);
          img{
            transform: scale(1.12);
          }
        }
        img{
          transition: transform .7s;
          object-fit: cover;
        }
        span{
          position: absolute;
          top: 0;
          left: 0;
          display: flex;
          justify-content: flex-end;
          flex-direction: column;
          align-items: flex-start;
          width: 100%;
          height: 100%;
          padding: 48px;
        }
      }
    }
  }
}