.block16{
  .info{
    position: relative;
    height: calc(100Vh - 165px);
    padding: 185px 0 0;
    text-align: center;
    h1{
      position: relative;
      z-index: 1;
    }
    .desc{
      position: relative;
      max-width: 854px;
      padding: 10px 0 0;
      margin: 0 auto;
      z-index: 1;
    }
    img{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      transition: none;
    }
  }
  .h3{
    padding: 108px 0 0;
    p{
      padding: 0 0 10px;
      font-size: 24px;
      font-weight: 600;
      line-height: 32px;
      &:last-child{
        padding: 0;
      }
      span{
        color: var(--color-text-hover);
      }
    }
  }
}