.block8{
  .swiper{
    margin: 32px 0 0;
    .swiper-wrapper{
      align-items: stretch;
      .swiper-slide{
        display: grid;
        grid-template-rows: 1fr;
        height: auto;
        ul{
          display: grid;
          grid-template-columns: 1fr;
          li{
            display: grid;
            grid-template-columns: 1fr;
            min-height: 50%;
            padding: 0 0 16px;
            &:last-child{
              padding: 0;
            }
            a{
              flex: 1 1 auto;
              flex-flow: row nowrap;
              gap: 16px;
              width: 100%;
              span{
                &.img{
                  max-width: 190px;
                  min-width: 190px;
                  height: 190px;
                  padding: 0;
                }
              }
            }
          }
        }
        a{
          display: flex;
          flex-flow: column-reverse nowrap;
          justify-content: flex-end;
          padding: 24px;
          background: var(--color-white);
          &:hover{
            transform: scale(.97);
          }
          span{
            display: block;
            &.img{
              width: 100%;
              padding: 10px 0 0;
              overflow: hidden;
            }
            &.info{
              flex: 1 1 auto;
              span{
                &.date{
                  font-size: 18px;
                }
                &.desc{
                  padding: 13px 0 0;
                }
              }
              h3{
                padding: 30px 0 0;
              }
            }
          }
        }
      }
    }
    .actions{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 30px 0 0;
      .navs{
        display: flex;
        align-items: center;
        gap: 5px;
        button{
          width: 38px;
          height: 38px;
          &:hover{
            svg{
              use{
                stroke: var(--color-text-hover);
              }
            }
          }
          &:last-child{
            transform: rotate(180deg);
          }
          &.swiper-button-disabled{
            opacity: .5;
            cursor: default;
          }
          svg{
            use{
              stroke: var(--color-text-gray);
              stroke-width: 2;
            }
          }
        }
      }
    }
  }
}