.block7{
  ul{
    display: grid;
    grid-template-columns: calc(50% - 8px) calc(50% - 8px);
    gap: 16px;
    li{
      &:first-child{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding: 60px;
        color: var(--color-white);
        transition: transform .7s;
        background: linear-gradient(45deg,var(--color-animate-purple-10) 5.65%,var(--color-animate-purple-40) 94.35%);
        backdrop-filter: blur(9px);
        overflow: hidden;
        &:hover{
          transform: scale(.97);
        }
        .h1,
        .desc,
        a.btn{
          position: relative;
          z-index: 1;
        }
        .desc{
          padding: 32px 0 0;
        }
        a.btn{
          margin: 48px 0 0;
        }
      }
      &:last-child{
        min-height: 320px;
        overflow: hidden;
        video{
          width: 100%;
          height: 100%;
          aspect-ratio: 1/.95;
          object-fit: cover;
        }
      }
    }
  }
}