.block20{
  ul{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    padding: 30px 0 0;
    li{
      position: relative;
      min-height: 460px;
      padding: 38px 20px;
      background: var(--color-white);
      overflow: hidden;
      &:before{
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 100%;
        height: 100%;
        background: url(../img/logo_about_bg.png) top right no-repeat;
        background-size: auto 100%;
        content: "";
      }
      .desc{
        padding: 10px 0 0;
      }
    }
  }
}