.block19{
  position: relative;
  .navs{
    position: absolute;
    top: 14px;
    right: 0;
    display: flex;
    align-items: center;
    gap: 5px;
    button{
      width: 38px;
      height: 38px;
      &:hover{
        svg{
          use{
            stroke: var(--color-text-hover);
          }
        }
      }
      &:last-child{
        transform: rotate(180deg);
      }
      &.swiper-button-disabled{
        opacity: .5;
        cursor: default;
      }
      svg{
        use{
          stroke: var(--color-text-gray);
          stroke-width: 2;
        }
      }
    }
  }
  .swiper{
    padding: 20px 0 0;
    .swiper-wrapper{
      align-items: stretch;
      .swiper-slide{
        height: auto;
        padding: 30px 20px;
        background: var(--color-white-80);
        img{
          width: 67px;
          height: 67px;
        }
        .h3{
          padding: 20px 0 0;
        }
        .desc{
          padding: 20px 0 0;
          font-weight: 300;
        }
      }
    }
  }
}