.block1{
  display: grid;
  grid-template-columns: auto calc((100vh - 146px - 15px) / 2);
  gap: 14px;
  min-height: calc(100vh - 131px);
  .item{
    position: relative;
    z-index: 1;
    &:nth-child(1){
      .swiper{
        position: absolute;
        &.main{
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          transition: all .5s ease;
          &.animate1{
            top: -115px;
            left: -48px;
            width: calc(100% + 48px);
            height: 100vh;
            border-radius: 0;
            z-index: 2000;
          }
          &.animate2{
            width: 100Vw;
            height: 100vh;
          }
          .swiper-slide{
            &-active{
              .box{
                transform: scale(1)!important;
              }
            }
            .box{
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              transform: scale(1.3);
              button{
                position: absolute;
                top: 20px;
                right: 20px;
                width: 38px;
                height: 38px;
                z-index: 1;
                &.active{
                  svg{
                    &:first-child{
                      opacity: 0;
                      visibility: hidden;
                    }
                    &:last-child{
                      opacity: 1;
                      visibility: visible;
                    }
                  }
                }
                svg{
                  position: absolute;
                  top: 0;
                  left: 0;
                  transform: scale(.8);
                  &:hover{
                    transform: scale(1);
                  }
                  &:first-child{
                    opacity: 1;
                    visibility: visible;
                  }
                  &:last-child{
                    opacity: 0;
                    visibility: hidden;
                  }
                  use{
                    stroke: var(--color-white);
                    stroke-width: 1.5;
                  }
                }
              }
              video,
              img{
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
              }
            }
          }
        }
        &.thumb{
          bottom: 24px;
          left: 24px;
          width: 430px;
          max-width: calc(100% - 48px);
          padding: 30px 30px 20px;
          background: var(--color-white-70);
          backdrop-filter: blur(5px);
          z-index: 2001;
          &.opacity{
            opacity: 0;
          }
          .swiper-slide{
            opacity: 0;
            transition: opacity .3s ease;
            &-active{
              opacity: 1;
            }
            .desc{
              padding: 13px 0 0;
            }
            a{
              margin: 20px 0 0;
            }
          }
          .navs{
            position: absolute;
            bottom: 20px;
            right: 30px;
            display: flex;
            align-items: flex-end;
            gap: 7px;
            height: 0;
            z-index: 1;
            button{
              width: 38px;
              height: 38px;
              &:hover{
                svg{
                  use{
                    stroke: var(--color-text-hover);
                  }
                }
              }
              &:last-child{
                transform: rotate(180deg);
              }
              svg{
                use{
                  stroke: var(--color-text-gray);
                  stroke-width: 2;
                }
              }
            }
          }
        }
      }
    }
    &:nth-child(2){
      display: grid;
      grid-template-rows: 1fr 1fr;
      gap: 16px;
      a{
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 35px 32px 26px;
        border-radius: 15px;
        background: var(--color-white-70);
        transform: scale(1);
        transition: transform .7s;
        &:hover{
          transform: scale(.97);
        }
        .gallery{
          position: relative;
          width: 66%;
          margin: auto auto 17px;
          border-radius: 50%;
          background: linear-gradient(180deg, transparent 65.85%, var(--color-black-07) 74.67%, transparent 95.12%), var(--color-gradient-gray);
          aspect-ratio: 1 / 1;
          .swiper{
            position: absolute;
            left: 50%;
            margin-left: -162px;
            width: 324px;
          }
        }
        .info{
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: auto;
          img{
            max-width: 43px;
            min-width: 43px;
            height: 43px;
          }
        }
      }
    }
  }
}