.block4{
  position: relative;
  z-index: 1;
  .swiper{
    .swiper-wrapper{
      align-items: stretch;
      .swiper-slide{
        height: auto;
        padding: 30px 20px 50px;
        border: 1px solid var(--color-black-10);
        border-radius: 15px;
        background: var(--color-text-transparent);
        transition-property: all;
        &:hover{
          border-color: var(--color-white);
          background: var(--color-white);
        }
        img{
          width: 67px;
          height: 67px;
        }
        h3{
          padding: 32px 0 0;
        }
        .desc{
          padding: 12px 0 0;
        }
      }
    }
    .actions{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 30px 0 0;
      .navs{
        display: flex;
        align-items: center;
        gap: 5px;
        button{
          width: 38px;
          height: 38px;
          &:hover{
            svg{
              use{
                stroke: var(--color-text-hover);
              }
            }
          }
          &:last-child{
            transform: rotate(180deg);
          }
          &.swiper-button-disabled{
            opacity: .5;
            cursor: default;
          }
          svg{
            use{
              stroke: var(--color-text-gray);
              stroke-width: 2;
            }
          }
        }
      }
    }
  }
}