section.block11{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  padding: 32px 0 0;
  h1{
    position: relative;
    z-index: 1;
  }
  .info{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 1;
    .desc{
      width: 100%;
      font-size: 18px;
      &:nth-child(1){
        color: var(--color-text-hover);
      }
      &:nth-child(2){
        padding: 10px 0 0;
        text-align: right;
      }
    }
    a.btn{
      width: auto;
      margin: auto 0 0;
    }
  }
}