.block18{
  .h1{

  }
  ul{
    display: grid;
    grid-template-columns: 58% 42%;
    gap: 16px;
    grid-template-areas: "one two" "three three";
    padding: 30px 0 0;
    li{
      position: relative;
      &:nth-child(1){
        grid-area: one;
      }
      &:nth-child(2){
        grid-area: two;
      }
      &:nth-child(3){
        grid-area: three;
      }
      img{
        width: 100%;
        height: 100%;
      }
      .h2{
        position: absolute;
        bottom: 40px;
        left: 30px;
        color: var(--color-white);
      }
    }
  }
}